.App {
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.Menu,
.Quiz,
.EndScreen {
  width: 31.25rem;
  height: 31.25rem;
  border-radius: 10px;
  background-color: lightseagreen;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #fff;
}

.Menu button,
.Quiz button,
.EndScreen button {
  width: 18.75rem;
  height: 3.125rem;
  margin: 0.3125rem;
  border: none;
  border-radius: 5px;
  font-size: 1.25rem;
  cursor: pointer;
}

.Quiz button:hover,
.Quiz button:active {
  border: #000;
  color: green;
}

.options {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
